<template>
  <div class="content">
    <BannerView></BannerView>
      <div class="newsList">
        <ul>
            <li v-for="(item,index) in arrlist">
              <router-link :to="'/article/'+item.id">
                <h2> {{index+1}} {{item.title}} </h2>
                <p> {{item.detail}} </p>
              </router-link>
            </li>
       </ul>
      </div>
  </div>
</template>
<script>
import BannerView from './BannerView.vue'
  export default{
    components:{
      BannerView
    },
    data(){
     return {
      arrlist: []
     }
    },
    mounted(){
      this.fetchDate()
    },
    methods:{
      fetchDate(){
        var _this = this 
        this.$http.get('src/data/index.data').then(function(res){
          setTimeout(function(){
          _this.arrlist = res.data
          },2000)
        }).catch(function(err){console.log('Home',err)})
      }
    }
  }
</script>



