
 const normalTime=(input)=>{
	if(input){
		var oData = new Date()
		oData.setTime(input)
		var y = oData.getFullYear()
		var m = oData.getMonth() + 1
		var d = oData.getDate()
		var h = oData.getHours()
		var mm = oData.getMinutes()
		var s = oData.getSeconds()
	};

	const toDou=(n)=>{
		if(n<10){
            return '0'+n
		}else{
			return n
		}
	};

   return toDou(y)+'-'+toDou(m)+'-'+toDou(d)+'-'+toDou(h)+':'+toDou(mm)+':'+toDou(s)
}

export {
	normalTime
}


