import HomeView from './components/HomeView.vue'
import FollowView from './components/FollowView.vue'
import ColumnView from './components/ColumnView.vue'
import UserInfo from './components/UserView.vue'
import Article from './components/Article.vue'
export default [
    {path:'/home',component:HomeView},
    {path:'/follow',component:FollowView},
    {path:'/column',component:ColumnView},
    {path:'/home',component:HomeView},
    {path:'/column',component:ColumnView},
    {path:'/user-info',component:UserInfo},
    {path:'/article/:id',component:Article},
    {path: '*',redirect: '/home'}
]



