import getters from './getters'

const state = {
	header:true,
	loadingheader:false
}

const mutations = {
	hideHeader:(state)=>{
	   return state.header = false
	},
	showHeader:(state)=>{
	   return state.header = true
	},
	showLoading:(state)=>{
	   return state.loadingheader = true
	},
	hideLoading:(state)=>{
		return state.loadingheader = false
	}
}

export default {
	state,
	getters,
	mutations
}



